import React, { useState } from "react";
import { assets } from "../assets/assets";
import { NavLink } from "react-router";

const Header = () => {
  const [token, setToken] = useState(true);

  return (
    <div className="flex items-center justify-between bg-white text-sm px-6 py-4 md:px-20 mb-5 border-b border-gray-400 sticky top-0 z-10">
      <div className="w-40 cursor-pointer">
        <img
          src="https://media-assets.swiggy.com/swiggy/image/upload/fl_lossy,f_auto,q_auto,w_260/portal/m/seo/Logos/Swiggy/Swiggy%20logo-2.png"
          width="100%"
          alt="logo"
        />
      </div>

      <ul className="hidden md:flex items-start gap-8 font-normal text-lg text-slate-500">
        <NavLink to="/">
          <li>
            <i className="bi bi-house-fill mr-2"></i> HOME
          </li>
          <hr className="border-none outline-none h-0.5 bg-black w-3/5 m-auto hidden" />
        </NavLink>
        <NavLink to="/about">
          <li>
            <i className="bi bi-file-person mr-2"></i>ABOUT
          </li>
          <hr className="border-none outline-none h-0.5 bg-black w-3/5 m-auto hidden" />
        </NavLink>
        <NavLink to="/contact">
          <li>
            <i className="bi bi-telephone-fill mr-2"></i>CONTACT
          </li>
          <hr className="border-none outline-none h-0.5 bg-black w-3/5 m-auto hidden" />
        </NavLink>
        <NavLink to="/cart">
          <li>
            <i className="bi bi-bag-fill mr-2"></i>CART
          </li>
          <hr className="border-none outline-none h-0.5 bg-black w-3/5 m-auto hidden" />
        </NavLink>
      </ul>

      <div className="flex items-center gap-4">
        {token ? (
          <button
            onClick={() => setToken(false)}
            className="bg-orange-500 px-4 py-3 text-white rounded-full"
          >
            Create Account
          </button>
        ) : (
          <div className="group relative">
          <img src={assets.account_img} className="w-11" alt="account" />
            <div className="group-hover:block hidden absolute dropdown-menu right-0 pt-1 -z-20">
                <div className='flex flex-col gap-2 font-normal text-lg text-slate-500 w-40 py-4 px-6 bg-slate-100 rounded'>
                    <p className="cursor-pointer hover:text-black">My Profile</p>
                    <p className="cursor-pointer hover:text-black">Orders</p>
                    <p className="cursor-pointer hover:text-black">Logout</p>
                </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default Header;
